* {
  margin: 0;
  padding: 0; }

html, body {
  font-family: 'Jura', sans-serif;
  width: 100%;
  height: 100%;
  background: 100% 100% no-repeat;
  background-size: cover;
  z-index: -2;
  color: white; }

.header {
  width: 1100px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  padding-top: 30px; }

.main {
  width: 1100px;
  height: 65%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between; }

.cover {
  position: fixed;
  top: 0px;
  background: contain;
  z-index: -1;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.75); }

.background-container {
  position: fixed;
  top: 0px;
  z-index: -2;
  width: 100%;
  height: 100%;
  background-size: cover; }

.button {
  height: 50px;
  width: 50px;
  border-radius: 15px;
  background-color: rgba(0, 0, 0, 0.253);
  color: white;
  border: 2px solid white;
  font-size: 20px;
  cursor: pointer;
  outline: none; }

.map {
  width: 300px;
  height: 300px;
  border-radius: 20px;
  border: 2px solid white;
  overflow: hidden;
  margin: 2%; }

.weather {
  display: grid;
  grid-template-columns: 8fr 1fr;
  width: 100%;
  height: 50%; }

.search-button {
  width: 100px; }

button:hover {
  background-color: #c2c2c2; }

.buttons-panel__refresh {
  width: 100px;
  margin-right: 20px; }

.buttons-panel__switchTemperatureToF {
  margin-left: 20px; }

.active {
  background-color: rgba(119, 119, 119, 0.671); }

.weather__location, .eather__town, .weather__country, .weather__time, .weather__date, .weather__curent-condition, .weather__humidity, .weather__temperature, .weather__wind-speed {
  text-align: center; }

.weather__location {
  font-size: 25px; }

.weather__time, .weather__date {
  font-size: 20px; }

.weather__current-text, .weather__temperature {
  font-size: 35px; }

.weather__today {
  height: 400px; }

.add-weather {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-column: 1/3; }

.add-weather__cell {
  height: 200px;
  margin: 2%;
  padding: 1%;
  border: 2px solid white;
  border-radius: 20px;
  padding-top: 10px;
  padding-left: 10px; }

.add-weather__weather-date {
  text-align: center;
  font-size: 20px; }

.add-weather__weather-condition-in-this-day {
  height: 33px;
  text-align: center; }

.add-weather__temperature_in_this_day {
  font-size: 35px;
  padding-top: 15px; }

.weather-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding-top: 20px; }

.search-form__input {
  font-family: 'Jura', sans-serif;
  height: 50px;
  background-color: rgba(177, 177, 177, 0.014);
  outline: none;
  color: white;
  font-size: 15px;
  border: 2px solid white;
  border-radius: 10px;
  font-size: large;
  width: 190px; }

.weather__ico {
  padding-top: 86px;
  padding-right: 67px; }

@media (max-width: 1130px) {
  .main {
    width: 95%; }
  .header {
    width: 95%; } }

@media (max-width: 800px) {
  .main {
    display: grid;
    width: 95%;
    grid-template-rows: 1fr 1fr; }
  .header {
    width: 95%;
    display: grid;
    grid-template-rows: 1fr 1fr;
    grid-template-columns: 1fr; }
  .map {
    width: 90%;
    margin: 0 auto; }
  .background-container {
    position: fixed;
    top: 0px;
    height: 100%; }
  .cover {
    position: fixed;
    top: 0px;
    height: 100%; }
  .buttons-panel {
    margin: 0 auto; }
  .search-form {
    margin: 0 auto; }
  .coordinates {
    text-align: center; }
  .search-form__input {
    width: 306px; } }

@media (max-width: 650px) {
  .main {
    width: 90%; }
  .header {
    width: 90%; }
  .add-weather {
    display: grid;
    grid-template-columns: 1fr; }
  .background-container {
    position: fixed;
    top: 0px;
    height: 100%;
    background: contain; }
  .cover {
    position: fixed;
    top: 0px;
    height: 100%;
    background: contain; }
  .map-container {
    padding: 0px; }
  .add-weather__temperature_in_this_day {
    text-align: center;
    padding-top: 0px; }
  .add-weather__cell {
    height: 160px; }
  .map {
    width: 95%; }
  .add-weather__weather-condition-in-this-day {
    font-size: 20px; } }
